import { axiosUtil } from '@/utils/axios'
export default {
  data: () => ({
    form: {},
    valid: true,
    error: null,
    success: null,
    rules: {
      prefix: [(v) => !!v || 'กรุณากรอกคำนำหน้าชื่อ'],
      first_name: [(v) => !!v || 'กรุณากรอกชื่อ'],
      last_name: [(v) => !!v || 'กรุณากรอกนามสกุล'],
      _id: [(v) => !!v || 'กรุณากรอกรหัสนักศึกษา'],
      faculty: [(v) => !!v || 'กรุณากรอกคณะ'],
      department: [(v) => !!v || 'กรุณากรอกภาควิชา'],
      gender: [(v) => !!v || 'กรุณาเลือกเพศ'],
      address: [(v) => !!v || 'กรุณากรอกที่อยู่'],
      username: [(v) => !!v || 'กรุณากรอกชื่อผู้ใช้'],
      confirmPassword: [(v) => v === this.form.password || 'รหัสผ่านไม่ตรงกัน'],
      email: [(v) => !!v || 'กรุณากรอกอีเมล์'],
      tel: [(v) => !!v || 'กรุณากรอกเบอร์โทรศัพท์'],
    },
  }),

  methods: {
    async edit() {
      await axiosUtil
        .getInstance()
        .put('/user', this.form)
        .then(() => {
          this.success = 'อัพเดทสำเร็จ'
        })
        .catch((err) => {
          this.error = err
        })
    },
  },

  computed: {
    pictureUrl() {
      return this.$store.getters['me/GET_PICTURE_URL']
    },
  },

  async created() {
    const me = await this.$store.dispatch('me/fetchProfile')
    delete me.password
    this.form = me ?? {}
  },
}
